
import NoData from "../../components/noData";
import Scroll from "@/components/scrollAll";

import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
components: {
XHeader,
Tab,
TabItem,
Sticky,
Datetime,
Group,
Cell,
Scroll,
NoData
},
data() {
return {
PageTab:0,
SecondTab:1,
dayParams: {
orgId:0,
startTime:dateFormat(new Date(), "YYYY-MM-DD") + " 00:00:00",
endTime:dateFormat(new Date(), "YYYY-MM-DD") + " 23:59:59",
},
monthParams:{
year:'',
month:'',
orgId:0,
},
dayCheckedList:[],
dayUncheckList:[],
monthCount:{
Count:0,
UnCount:0
},
selectedMonth:dateFormat(new Date(), "YYYY-MM"),
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
};
},
mounted(){
let _date = new Date()
this.monthParams.year = _date.getFullYear()
this.monthParams.month = _date.getMonth() + 1
this.getDayList()
},
computed:{
checkedNum(){
return this.dayCheckedList.length
},
unCheckedNum(){
return this.dayUncheckList.length
},
dateSelected(){
return dateFormat(new Date(this.dayParams.startTime), "YYYY-MM-DD")
},
monthAll(){
return this.monthCount.Count+this.monthCount.UnCount
}
},
methods: {
changeSecondTab(tab){
this.SecondTab=tab
},
showMonthPlugin () {
let _this = this
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD',
value: _this.dateSelected,
onConfirm (val) {
_this.dayParams.startTime = val + " 00:00:00"
_this.dayParams.endTime = val + " 23:59:59"
_this.getDayList()
}
})
},
monthChange(value){
let _date = new Date(value)
this.monthParams.year = _date.getFullYear()
this.monthParams.month = _date.getMonth() + 1
this.getMonthInfo().then(()=>{})
},
pageScroll(){
this.$refs.scroll.scroll.finishPullUp();
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
},
getDayList(){
let _url = "/Statistics/GetWorkSinginUser"
return this.getAxios(_url, this.dayParams).then(res=>{
if(res.State == 1){
this.dayCheckedList = res.Data.Singin
this.dayUncheckList = res.Data.UnSingin
}else{
this.$vux.loading.hide()
}
}).catch((res)=>{
this.$vux.toast.show({
text: res.ErrorMessage,
position: "middle",
type: "cancel",
onHide() {
}
});
this.$vux.loading.hide()
})
},
getMonthInfo(){
let _url = "/Statistics/GetWorkSinginMonth";
return this.getAxios(_url, this.monthParams).then(res=>{
if(res.State == 1){
this.monthCount.Count = res.Data.Count
this.monthCount.UnCount = res.Data.UnCount
}else{
this.$vux.loading.hide()
}
}).catch((res)=>{
this.$vux.toast.show({
text: res.ErrorMessage,
position: "middle",
type: "cancel",
onHide() {
}
});
this.$vux.loading.hide()
})
},
switchTabItem (index) {
this.$vux.loading.show({
text: 'loading'
})
if(index == 0){
this.getDayList().then(()=>{
this.PageTab = index
this.$vux.loading.hide()
})
}else{
this.getMonthInfo().then(()=>{
this.PageTab = index
this.$vux.loading.hide()
})
}


},
},
filters: {
dateFormat,
getPicSrc(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
}
}
};



